body {
  background-color: #aaa;
}

.breed-select {
  width: 350px;
  height: 50px;
  color: #603;
  scrollbar-width: thin;
  scrollbar-color: #603 transparent;
  background-color: rgba(0, 0, 0, 0);
  border: dashed #603;
  border-radius: 10px;
  margin-top: 20px;
  margin-left: 20px;
  font-family: Bagel Fat One, cursive;
  font-size: 26px;
  line-height: 30px;
  display: inline-block;
  position: relative;
  overflow-y: auto;
}

.breed-select option {
  background-color: #fae1f4;
}

.breed-select::-webkit-scrollbar {
  width: 8px;
}

.breed-select::-webkit-scrollbar-track {
  background-color: rgba(0, 0, 0, 0);
}

.breed-select::-webkit-scrollbar-thumb {
  background-color: #603;
}

.cat-info {
  width: 960px;
  background-color: #edfffd;
  border-radius: 30px;
  margin: 30px auto;
  display: flex;
  box-shadow: 0 2px 18px rgba(0, 0, 0, .3);
}

.breed-image {
  width: 500px;
  border-radius: 30px 0 0 30px;
}

.breed-container {
  width: 400px;
  padding: 20px;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  background: linear-gradient(0deg, rgba(255, 61, 0, .2) 33%, #ff3d00 100%);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 44px;
  height: 44px;
  background: #263238;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.breed-name {
  color: #ff3d00;
  font-family: Manrope, sans-serif;
  font-size: 46px;
}

.breed-temperament {
  color: #ff3d00;
  font-family: Manrope, sans-serif;
  font-size: 24px;
  font-weight: 600;
  line-height: 1.5;
}

.breed-description {
  color: #404040;
  font-family: Manrope, sans-serif;
  font-size: 18px;
  line-height: 1.5;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

